.post{
    width: 550px;
    margin: 30px 40px 50px 40px;
}
.post__header{
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    margin-bottom: 10px;
}
.post__headerAuthor{
    display: flex;
    flex-direction: row;
    align-items: center;
    font-weight: bolder;
}
.post__headerAuthor >span{
    color: grey;
    font-size: 15px;

}
.post__image > img{
    width: 100%;
    border: 0.6px solid rgba(128, 128, 128,0.516);
    border-radius: 6px;
}
.post__footerIcons{
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
}
.postIcon:hover{
    cursor: pointer;
}
.postIcon{
    padding: 7px;
    font-size: 30px;
}